import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {ITransferServiceProvider} from "../../providers/i-transfer-service/i-transfer-service";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";


@IonicPage()
@Component({
  selector: 'page-i-transfer01',
  templateUrl: 'i-transfer01.html',
})
export class iTransfer01 {
  iTransferError01;
  accounts = [];
  transactional;
  sourceAcc;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public tokenService: TokenServiceProvider,
              public storage: LocalStorageProvider,
              public maskTokenUtil: MaskTokenProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public transferService: ITransferServiceProvider,
              public gaProvider: GoogleAnalyticsProvider) {

    let dAccount = this.storage.retrieveFromStorage('accounts');
    if (dAccount == null || dAccount == "") {
      this.accounts = [];
    }
    else {

      dAccount = cryptoUtil.getDecryptVal(dAccount);
      dAccount = JSON.parse(dAccount);
      Object.keys(dAccount).forEach(key => {
        let value = dAccount[key];
        if (value.debitApproved == true) {
          this.accounts.push(value);
        }
      });
    }


    let dWelCome = this.storage.retrieveFromStorage('welcomeVal');
    if (dWelCome == null || dWelCome == "") {
    }
    else {
      dWelCome = cryptoUtil.getDecryptVal(dWelCome);
      dWelCome = JSON.parse(dWelCome);
      this.transactional = dWelCome.transactional;
    }

  }

  iTransfer01Action = function (account) {
    this.iTransferError01 = "";

    if (account.debitApproved == false) {
      this.iTransferError01 = "This Account is not permitted for iRecharge.";
      return;
    }

    if (this.transactional == 1) {
      this.sourceAcc = account.accountNo;
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
    else {
      this.navCtrl.push('TxErrorPage');
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encAccNo = this.cryptoUtil.getEncryptVal(this.sourceAcc);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.transferService.fetchDestAndPurposeList(unMaskToken, appData.userId, appData.appId, encAccNo)
        .then(data => {
          this.onFetchDestAndPurposeListComplete(data);
        });
    }
  };

  onFetchDestAndPurposeListComplete = function (data) {
    let sourcePage = "transfer";
    if (data.statusCode == 1) {
      this.navCtrl.push('ITransfer02Page', {
        'accountNo': this.sourceAcc,
        'transferDetails01': JSON.stringify(data.object),
        'sourcePage': JSON.stringify(sourcePage)
      });
    }

    else if (data.statusCode == 0) {
      this.iTransferError01 = data.note;
    }

    else {
      this.iTransferError01 = "System Error Contact with Administrator.";
    }
  };

  goToTransferHistoryAction = function () {
    this.navCtrl.push('ITransferHistory01Page');
  };

  goToManageCreditAccountAction = function () {
    this.navCtrl.push('ManageCreditAccount01Page');
  };


  ionViewDidLoad() {
    this.gaProvider.startTrackerWithId("iTransfer");
  }

}



